body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.contenedor-preguntas {
  margin-top: 10px;
  padding: 8px;
}

.cuadro-imagen > img {
  width: 100%;
}


.checked-image {
  position: absolute;
  top: 0;
  right: 15px;
  background-color: #fff;
  width: 25px;
  height: 20px;
  padding: 2px;
}

.img-op  {
  width: 100%;
}

.hidden {
  display: none;
}

.image-check {
  border: 0;
  background-color: #fff;
  padding: 0;
}

.form-encuesta {
  margin: 30px 10px;
  padding: 30px;
}

.content-preguntas {
  margin-top: 40px;
}

.mensaje {
  margin-top: 34vh;
  align-content: center;
}

.mensaje > .card {
  margin: 0 auto;
  max-width: 400px;
}

.image-form {
  margin-top: 10px;
}